import {getResources} from '../services/requests';

const showStyle = (triggerSelector, wrapper) => {
    const btn = document.querySelector(triggerSelector);
/*           div = Array.from(containerDiv.children),
          

    btn.addEventListener('click', () => {
        
        div.forEach (item => {
            item.className = '';
            item.classList.add('col-sm-3', 'col-sm-offset-0', 'col-xs-10', 'col-xs-offset-1');
            item.classList.add('animated', 'fadeInUp');

            btn.remove();
        });
    }); */

    btn.addEventListener('click', function () {
        getResources('http://localhost:3000/styles')
            .then(res => creatCards(res))
            .catch(error => console.log(error));

        this.remove();
    });

    function creatCards(response) {
        response.forEach(item => {
            let card = document.createElement('div');

            card.classList.add('animated', 'fadeInUp', 'col-sm-3', 'col-sm-offset-0', 'col-xs-10', 'col-xs-offset-1');

            card.innerHTML = `
            <div class="styles-block">
                <img src=${item.src}>
                <h4${item.title}</h4>
                <a href=${item.link}>Подробнее</a>
            </div>
            `;

            document.querySelector(wrapper).appendChild(card);
        });
    }
};

export default showStyle;